@import 'Mixin.scss';

.contact {
    h2 {
        @include h2-common-style;
        margin-top: 90px;
    }

    h5{
        font-size: 20px;
        font-weight: normal;
        color:  rgba(121, 156, 165);
        border: 1px white solid;
        border-radius: 25px;
        padding: 20px;
        font-style: italic;
        margin: 0px;
    }

    h6{
        color: white;
        font-weight: 300;
    }

    .contactDescription {
        width: 80%;
        margin: auto;
        margin-bottom: 90px;
    }

    .contactEmail {
        @include contact-common-style;
        width: 23%;
        margin-bottom: 30px;
        .iconeMail {
            width: 40px;
        }
        .mailTo {
            @include link-common-style;       
        }
    }
    
    .contactPhone {
        @include contact-common-style;
        width: 16%;
        margin-bottom: 15px;
        .iconePhone {
            width: 40px;
            height: 41px;
        }
        .phoneNumber {
            font-size: 20px;
            color: rgb(121, 156, 165);
        }
    }

    .localisation {
        @include contact-common-style;
        width: 11%;
        align-items: center;
        .iconeLocalisation {
            width: 40px;
            margin-bottom: 10px;   
        }
        .toLocalisation {
            @include link-common-style;
        }  
    }
}

@media screen and (max-width: 1265px) {
    .contact {
        .contactEmail {
            .mailTo {
                margin-left: 15px;
            }
        }
        .contactPhone {
            .phoneNumber {
                margin-left: 10px;
            }
        }
        .localisation {
            .toLocalisation {
                margin-left: 10px;
            }
        }
    }
}

@media screen and (max-width: 440px) {
    .contact {
        h2{
            font-size: 25px;
        }

        h5, h6{
            font-size: 20px;
            width: 100%;
            margin-left: -20px;
        }

        .contactEmail {
            .mailTo {
                font-weight: bold;
                font-size: medium;
                margin-right: 45px;
            }
            .iconeMail{
                visibility: hidden;
            }

        }
        .contactPhone {
            .phoneNumber {
                font-weight: bold;
                font-size: medium;
                margin-right: 45px;
                margin-top: -15px;
            }
            .iconePhone{
                visibility: hidden;
            }
        }
        .localisation {
            .toLocalisation {
                font-weight: bold;
                font-size: medium;
                margin-right: 45px;
                margin-top: -60px;
            }
            .iconeLocalisation{
                visibility: hidden;
            }
        }
    }
}